body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
a {
  color: #000;
  text-transform: none;
  text-decoration: none;
}
a:hover {
  color: #000;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
h1,
h2 {
  margin: 0;
  padding: 0;
}
div {
  box-sizing: border-box;
}
img {
  width: 100%;
  height: auto;
}
form {
  margin: 0;
}
@font-face {
  font-family: "Gilroy";
  src: url("../fonts/Gilroy-Regular.ttf") format("ttf"), url("../fonts/Gilroy-Regular.eot") format("eot"), url("../fonts/Gilroy-Regular.woff") format("woff");
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Gilroy-Bold";
  src: url("../fonts/Gilroy-Bold.ttf") format("ttf"), url("../fonts/Gilroy-Bold.eot") format("eot"), url("../fonts/Gilroy-Bold.woff") format("woff");
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf") format("ttf");
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Geometria";
  src: url("../fonts/Geometria.ttf") format("ttf"), url("../fonts/Geometria.eot") format("eot"), url("../fonts/Geometria.woff") format("woff");
  font-style: normal;
  font-display: swap;
}
.layout__wrapper {
  max-width: 1597px;
  width: 100%;
  margin: 0 auto;
  position: relative;
}
@media (max-width: 1597px) {
  .layout__wrapper {
    max-width: none;
    width: 83%;
  }
}
@media (max-width: 767px) {
  .layout__wrapper {
    width: 100%;
  }
}
.layout__title {
  text-align: center;
  font-family: "Gilroy-Bold", Arial, sans-serif;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  color: #3a393c;
  margin-bottom: 54px;
}
@media (max-width: 767px) {
  .layout__title {
    margin: 0 auto 27px auto;
    max-width: 320px;
  }
}
.link__key {
  width: 52px;
  height: 52px;
  position: absolute;
  right: 16px;
  bottom: 16px;
  display: none;
}
.link__key img {
  width: 100%;
  height: auto;
}
@media (max-width: 767px) {
  .link__key {
    display: block;
  }
}
.sort__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
}
.sort__wrapper .search__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: row;
  margin-bottom: 15px;
}
.sort__wrapper .search__wrapper .input__wrapper {
  width: 399px;
  min-width: 399px;
  padding: 12px;
  margin-right: 8px;
  border: 1px solid rgba(58,57,60,0.2);
  background-color: #fff;
  box-sizing: border-box;
}
.sort__wrapper .search__wrapper .input__wrapper input {
  border: none;
  outline: none;
  box-shadow: none;
  font-size: 14px;
  line-height: 16px;
  font-family: "Roboto", Arial, sans-serif;
  color: rgba(58,57,60,0.78);
  width: 100%;
}
.sort__wrapper .search__wrapper .input__wrapper input:placeholder {
  font-family: "Roboto", Arial, sans-serif;
}
.sort__wrapper .search__wrapper .search__btn {
  border: none;
  box-shadow: none;
  width: 44px;
  min-width: 44px;
  height: 44px;
  background: #3a393c url("../img/news/search.svg") no-repeat 50% 50%;
  cursor: pointer;
  border-radius: 0;
}
.sort__wrapper .search__wrapper .search__btn img {
  width: 20px;
  height: 20px;
}
@media (max-width: 1597px) {
  .sort__wrapper .search__wrapper {
    margin-bottom: 8px;
  }
  .sort__wrapper .search__wrapper .input__wrapper {
    width: 183px;
    min-width: 183px;
    padding: 6px 12px;
  }
  .sort__wrapper .search__wrapper .input__wrapper input {
    font-size: 12px;
    line-height: 14px;
  }
  .sort__wrapper .search__wrapper .search__btn {
    width: 32px;
    min-width: 32px;
    height: 32px;
  }
}
@media (max-width: 1365px) {
  .sort__wrapper .search__wrapper {
    margin-bottom: 12px;
  }
  .sort__wrapper .search__wrapper .input__wrapper {
    width: 114px;
    min-width: 114px;
  }
}
@media (max-width: 767px) {
  .sort__wrapper .search__wrapper {
    margin-bottom: 0;
    margin-left: 15%;
  }
  .sort__wrapper .search__wrapper .input__wrapper {
    width: auto;
    min-width: auto;
    flex-grow: 1;
  }
  .sort__wrapper .search__wrapper .search__btn {
    margin-right: 16px;
  }
}
.sort__item {
  font-family: "Gilroy", Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  white-space: nowrap;
  position: relative;
}
.sort__year {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  flex-grow: 1;
  margin-bottom: 15px;
}
@media (max-width: 1597px) {
  .sort__year {
    margin-bottom: 8px;
  }
}
@media (max-width: 1023px) {
  .sort__year {
    overflow-x: auto;
    width: 100%;
    margin-bottom: 20px;
  }
}
.sort__year .sort__item {
  color: rgba(51,51,51,0.6);
  margin-right: 40px;
}
@media (max-width: 1597px) {
  .sort__year .sort__item {
    margin-right: 33px;
  }
}
@media (max-width: 1365px) {
  .sort__year .sort__item {
    margin-right: 19px;
  }
}
@media (max-width: 767px) {
  .sort__year .sort__item:first-child {
    margin-left: 15%;
  }
}
.sort__year .sort__item-active {
  color: #0072c7;
  font-family: "Gilroy-Bold", Arial, sans-serif;
}
.sort__year .sort__item-active:after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background-color: #0072c7;
  position: absolute;
  bottom: -2px;
  left: 0;
}
.sort__btn {
  display: none;
}
@media (max-width: 767px) {
  .sort__btn {
    display: block;
    width: 32px;
    min-width: 32px;
    height: 32px;
    padding: 4px;
    box-sizing: border-box;
    background-color: #0072c7;
  }
  .sort__btn img {
    width: 100%;
    height: auto;
  }
}
.sort__topic {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  flex-wrap: wrap;
  align-self: center;
  width: 100%;
  padding-top: 16px;
  margin-bottom: 83px;
  border-top: 1px solid rgba(0,0,0,0.1);
}
.sort__topic .sort__item {
  color: rgba(0,0,0,0.6);
  padding: 8px 11px 8px 8px;
  margin-right: 17px;
}
.sort__topic .sort__item-active {
  color: #fff;
  background-color: #0072c7;
}
.sort__topic .close__wrapper {
  display: none;
}
@media (max-width: 1597px) {
  .sort__topic {
    padding-top: 8px;
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .sort__topic {
    display: none;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap: nowrap;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    z-index: 10000;
    overflow-y: auto;
    padding: 36px 12px 12px 12px;
  }
  .sort__topic-active {
    display: flex;
  }
  .sort__topic .sort__item {
    margin-right: 0;
    margin-bottom: 8px;
  }
  .sort__topic .close__wrapper {
    display: block;
    position: absolute;
    top: 12px;
    right: 36px;
    width: 24px;
    height: 24px;
  }
  .sort__topic .close__wrapper img {
    width: 100%;
    height: auto;
  }
  .sort__wrapper {
    margin-bottom: 31px;
  }
}
.empty__result {
  margin: 0 auto 120px auto;
  max-width: 400px;
}
.empty__result__img {
  width: 64px;
  height: 64px;
  margin: 0 auto 32px auto;
}
.empty__result__img img {
  width: 100%;
  height: auto;
}
.empty__result__text {
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  text-align: center;
  color: #3a393c;
  font-family: Gilroy-Bold, Arial, sans-serif;
}
.no-items__news {
  font-size: 24px;
  line-height: 29px;
  text-align: center;
  color: #3a393c;
  font-family: Gilroy, Arial, sans-serif;
  margin-bottom: 120px;
}
@media (max-width: 768px) {
  .empty__result {
    margin: 0 auto 40px auto;
  }
  .no-items__news {
    margin-bottom: 40px;
  }
}
.news__items {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  box-sizing: border-box;
}
@media (max-width: 1023px) {
  .news__items {
    justify-content: center;
  }
}
.news__item {
  width: 339px;
  min-width: 339px;
  height: 386px;
  margin-bottom: 40px;
  margin-right: 80px;
  text-decoration: none;
  overflow: hidden;
  position: relative;
}
@media (min-width: 1597px) {
  .news__item:nth-child(4n+4) {
    margin-right: 0;
  }
}
@media (max-width: 1596px) {
  .news__item {
    margin-right: 75px;
  }
  .news__item:nth-child(3n+3) {
    margin-right: 0;
  }
}
@media (max-width: 1400px) {
  .news__item {
    width: 385px;
    min-width: 385px;
    margin-bottom: 80px;
    margin-right: 80px;
    box-shadow: 0 5px 10px rgba(0,0,0,0.1);
  }
  .news__item:nth-child(3n+3) {
    margin-right: 80px;
  }
}
@media (max-width: 1124px) {
  .news__item {
    margin-right: 40px;
  }
  .news__item:nth-child(3n+3) {
    margin-right: 40px;
  }
  .news__item:nth-child(2n+2) {
    margin-right: 0;
  }
}
@media (max-width: 767px) {
  .news__item {
    width: 76%;
    min-width: auto;
    margin-bottom: 40px;
    margin-right: 0;
  }
  .news__item:nth-child(3n+3) {
    margin-right: 0;
  }
  .news__item .news__title {
    margin-bottom: 31px;
  }
  .news__item .news__text,
  .news__item .news__action__btns {
    display: none;
  }
}
.news__img__wrapper {
  width: 100%;
}
.news__img__wrapper img {
  width: 100%;
  height: auto;
}
.news__text__wrapper {
  display: block;
  padding: 19px;
  background-color: #fff;
}
.news__text__wrapper b {
  font-weight: normal;
  background: rgba(0,114,199,0.2);
}
.news__info {
  font-size: 14px;
  line-height: 13px;
  font-family: "Roboto", Arial, sans-serif;
  color: #bebebe;
  margin-bottom: 14px;
}
.news__text__section {
  height: 78px;
  overflow: hidden;
  width: 100%;
  margin-bottom: 17px;
}
@media (max-width: 767px) {
  .news__text__section {
    height: auto;
  }
}
.news__title {
  font-size: 14px;
  line-height: 19px;
  color: #3a393c;
  font-family: "Gilroy-Bold", Arial, sans-serif;
  text-transform: uppercase;
  margin-bottom: 4px;
}
.news__text {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: rgba(58,57,60,0.78);
  margin-bottom: 17px;
  height: 54px;
  -webkit-line-clamp: 3;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.news__action__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  opacity: 0;
}
.news__action__link {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #0072c7;
  position: relative;
}
.news__action__link:after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
  background-color: #0072c7;
  position: absolute;
  left: 0;
  bottom: -1px;
}
.news__action__link:hover {
  color: #0072c7;
}
.news__action__btns {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}
.news__action__btn {
  min-width: 16px;
  width: 16px;
  height: 16px;
  margin-right: 12px;
}
.news__action__btn img {
  width: 100%;
  height: auto;
}
.news__action__btn:last-child {
  margin-right: 0;
}
.news__item:hover {
  box-shadow: 0px 5px 10px rgba(0,0,0,0.1);
}
.news__item:hover .news__text__wrapper {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
.news__item:hover .news__action__wrapper {
  opacity: 1;
}
@media (max-width: 1280px) {
  .news__item {
    height: auto;
  }
  .news__action__wrapper {
    opacity: 1;
  }
  .news__item:hover .news__text__wrapper {
    position: relative;
    bottom: auto;
  }
}
.pagination__wrapper {
  display: flex;
  justify-content: center;
  margin-bottom: 119px;
}
.pagination__wrapper ul {
  margin: 0;
  display: flex;
  padding: 0;
}
.pagination__wrapper ul li {
  color: #a3a3a3;
  list-style: none;
  text-align: center;
  font-size: 16px;
  line-height: 19px;
  cursor: pointer;
  font-family: "Roboto", Arial, sans-serif;
  padding: 0 4px;
}
.pagination__wrapper ul li.numb {
  list-style: none;
  line-height: 19px;
  margin-right: 19px;
}
.pagination__wrapper ul li.dots {
  font-size: 16px;
  cursor: default;
  margin-right: 20px;
}
.pagination__wrapper ul li.prev {
  margin-right: 20px;
}
.pagination__wrapper ul li.prev:hover {
  color: #0072c7 !important;
}
.pagination__wrapper ul li.prev:before {
  content: '';
  display: inline-block;
  width: 13px;
  height: 8px;
  background: url("../img/news/arrow-pag-prev.svg") no-repeat 50% 50%;
  margin-right: 4px;
}
.pagination__wrapper ul li.prev:hover:before {
  background: url("../img/news/arrow-pag-prev-blue.svg") no-repeat 50% 50%;
}
.pagination__wrapper ul li.next:hover {
  color: #0072c7 !important;
}
.pagination__wrapper ul li.next:after {
  content: '';
  display: inline-block;
  width: 13px;
  height: 8px;
  background: url("../img/news/arrow-pag-next.svg") no-repeat 50% 50%;
  margin-left: 4px;
}
.pagination__wrapper ul li.next:hover:after {
  background: url("../img/news/arrow-pag-next-blue.svg") no-repeat 50% 50%;
}
.pagination__wrapper ul li.numb:hover,
.pagination__wrapper ul li:first-child:hover,
.pagination__wrapper ul li:last-child:hover {
  color: #a3a3a3;
}
.pagination__wrapper li.active,
.pagination__wrapper li.active.numb:hover {
  color: #fff;
  background-color: #0072c7;
}
@media (max-width: 767px) {
  .pagination__wrapper {
    margin-bottom: 74px;
  }
  .pagination__wrapper .btn.prev,
  .pagination__wrapper .btn.next {
    display: none;
  }
}
.mailing-form__wrapper {
  max-width: 780px;
  width: 100%;
  margin: 0 auto 114px auto;
  box-sizing: border-box;
  padding: 54px 0 58px 0;
  box-shadow: 0px 5px 10px rgba(0,0,0,0.1);
  position: relative;
}
@media (max-width: 1024px) {
  .mailing-form__wrapper {
    padding: 40px 20px;
  }
}
.mailing-form__wrapper:after {
  content: '';
  display: block;
  width: 66px;
  height: 85px;
  position: absolute;
  top: 0;
  left: 0;
  background: url("../img/news/mailing-bg.svg") no-repeat 50% 50%;
}
.mailing-form__wrapper:before {
  content: '';
  display: block;
  width: 101px;
  height: 112px;
  position: absolute;
  bottom: 0;
  right: 37px;
  background: url("../img/news/mailing-bg-2.svg") no-repeat 50% 50%;
}
@media (max-width: 1023px) {
  .mailing-form__wrapper:after,
  .mailing-form__wrapper:before {
    display: none;
  }
}
.mailing-form__text {
  font-size: 14px;
  line-height: 16px;
  font-family: "Roboto", Arial, sans-serif;
  color: rgba(58,57,60,0.78);
  text-align: center;
  margin-bottom: 33px;
}
.mailing-form__text-small {
  font-family: "Roboto", Arial, sans-serif;
  font-size: 12px;
  line-height: 14px;
  text-align: center;
  color: rgba(58,57,60,0.6);
}
.mailing-form__title {
  font-size: 24px;
  line-height: 29px;
  color: #3a393c;
  font-family: "Gilroy-Bold", Arial, sans-serif;
  text-align: center;
  margin-bottom: 7px;
  text-transform: uppercase;
}
.mailing-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  margin-bottom: 16px;
}
.mailing-form .input__wrapper {
  width: 100%;
  max-width: 356px;
  padding: 12px 16px;
  box-sizing: border-box;
  border: 1px solid rgba(58,57,60,0.2);
  font-family: "Roboto", Arial, sans-serif;
  margin-right: 8px;
}
.mailing-form .input__wrapper input[type="text"] {
  border: none;
  box-shadow: none;
  outline: none;
  padding: 0;
  font-family: "Roboto", Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
  width: 100%;
}
.mailing-form .input__wrapper input[type="text"]::placeholder {
  color: #bebebe;
  font-family: "Roboto", Arial, sans-serif;
}
.mailing-form .button__wrapper {
  width: 191px;
  height: 44px;
  padding: 13px 0 15px 0;
  background-color: #0072c7;
  border: none;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  font-family: "Gilroy", Arial, sans-serif;
  font-size: 14px;
  line-height: 16px;
}
@media (max-width: 1024px) {
  .mailing-form {
    margin-block-end: 0;
    margin-bottom: 40px;
  }
  .mailing-form__wrapper {
    padding: 50px 40px;
  }
  .mailing-form__text {
    font-size: 14px;
    line-height: 17px;
  }
  .mailing-form__title {
    font-size: 24px;
    line-height: 34px;
  }
  .mailing-form .input__wrapper {
    max-width: 380px;
    margin-right: 20px;
    margin-bottom: 0;
  }
  .mailing-form .button {
    width: 100%;
    max-width: 220px;
  }
}
@media (max-width: 768px) {
  .mailing-form__wrapper {
    margin-bottom: 80px;
    max-width: 620px;
  }
}
@media (max-width: 450px) {
  .mailing-form {
    flex-wrap: wrap;
  }
  .mailing-form__wrapper {
    max-width: none;
    padding: 40px;
    margin-bottom: 74px;
  }
  .mailing-form__wrapper:after {
    width: 50px;
    height: 50px;
    background-size: cover;
    top: -25px;
    right: 20px;
  }
  .mailing-form__title {
    font-size: 20px;
    line-height: 24px;
  }
  .mailing-form__text {
    font-size: 12px;
    line-height: 14px;
  }
  .mailing-form .input__wrapper {
    margin-bottom: 20px;
    margin-right: 0;
  }
  .mailing-form .button__wrapper {
    width: 100%;
    max-width: 100%;
    font-size: 12px;
    line-height: 14px;
    margin-bottom: 20px;
  }
}
.cards__section {
  overflow-x: auto;
  width: 100%;
  margin-bottom: 84px;
}
.cards__items__wrapper {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
}
.cards__items__wrapper .card__wrapper {
  width: 471px;
  border: 1px solid #dcdcdc;
  box-sizing: border-box;
  padding: 51px 20px 20px 20px;
  margin: 0 20px 20px 0;
  font-family: "Geometria", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #3a393c;
  position: relative;
}
.cards__items__wrapper .card__wrapper:last-child {
  margin-right: 0;
}
@media (max-width: 767px) {
  .cards__items__wrapper .card__wrapper {
    width: 223px;
    min-width: 223px;
  }
  .cards__items__wrapper .card__wrapper:first-child {
    margin-left: 15%;
  }
  .cards__items__wrapper .card__wrapper:last-child {
    margin-right: 15%;
  }
}
.cards__items__wrapper .card__slider {
  width: 100%;
  margin: 0 auto 14px auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}
.cards__items__wrapper .card__slider img {
  width: 100%;
  max-width: 200px;
  height: auto;
  max-height: 200px;
  margin: auto;
}
.cards__items__wrapper .card__row {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
